import React from 'react';
import { Link } from 'react-router-dom';
import myStyle from './NavSide.module.scss';

function NavSide() {
  return (
    <nav  className={myStyle.nav}> 
      <div className={myStyle.tabs}>
        <Link to="myBlog/mkTest" className={myStyle.tab}>mk test</Link>
        <Link to="myBlog/memoChart" className={myStyle.tab}>memoChart</Link>
        {/*   */}
      </div>
    </nav>
  );
}

export default NavSide;
