import React from 'react';
import {  Outlet } from 'react-router-dom';
import myStyle from './MainBlog.module.scss';

function MainBlog() {
  return (
    <div className={myStyle.container }>
      <div className={myStyle.subNav }></div>
      <div className={myStyle.subView }>
        <Outlet/> 
      </div>

    </div>
  );
}

export default MainBlog;
