import React , { useCallback } from 'react';
import myStyle from './FlowChart.module.scss';
import {  ReactFlow,  MiniMap,  Controls,  Background,  useNodesState,  useEdgesState,  addEdge,} from '@xyflow/react';
import '@xyflow/react/dist/style.css';
import { useNavigate } from 'react-router-dom';

//2024-09-01 
//pos js   
//initialEdges   js  
const initialNodes = [
  { id: 'ts1', position: { x: 0, y: 0 }, data: { label: '' } },
  { id: '2', position: { x: 0, y: 100 }, data: { label: '2' } },
  { id: '3', position: { x: 0, y: 200 }, data: { label: '3' } },
  { id: '4', position: { x: 0, y: 300 }, data: { label: '4' } },
  { id: '5', position: { x: 1000, y: 0 }, data: { label: 'php' } },
  { id: '6', position: { x: 1000, y: 100 }, data: { label: 'js' } },
  { id: '7', position: { x: 1000, y: 200 }, data: { label: '3' } },
  { id: '8', position: { x: 1000, y: 300 }, data: { label: '4' } },
];
const initialEdges = [
  { id: 'e1-2', source: 'ts1', target: '2' },
  { id: 'e1-3', source: '4', target: '3' },
  { id: 'e1-4', source: '4', target: '2' }

];

function FlowChart() {

  const navigate = useNavigate();
  const onNodeClick = (event, node) => {
    console.log('Node clicked:', node);
    navigate(`/your-target-page/${node.id}`); //   
  };

  const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
  const [edges, setEdges, onEdgesChange] = useEdgesState(initialEdges);

  const onConnect = useCallback(
    (params) => setEdges((eds) => addEdge(params, eds)),
    [setEdges],
  );


  return (
    <div className={myStyle.container }>
      <ReactFlow
        nodes={nodes}
        edges={edges}
        onNodesChange={onNodesChange}
        onEdgesChange={onEdgesChange}
        onConnect={onConnect}
        onNodeClick={onNodeClick}
      >
        <Controls />
        <MiniMap />
        <Background variant="dots" gap={12} size={1} />
      </ReactFlow>
    </div>
  );
}

export default FlowChart;
